body {
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  
  width: 5px;
}

body::-webkit-scrollbar-track {
  background: transparent;
}

body::-webkit-scrollbar-thumb {
  background:  #60A5FA;
  
}
.ImageSlider::-webkit-scrollbar {
  cursor:grab;
  border-radius: 10px;
  height: 8px;
}

.ImageSlider::-webkit-scrollbar-track {
  background: transparent;
}

.ImageSlider::-webkit-scrollbar-thumb {
  background:  #60A5FA;
  border-radius: 10px;
}

.ImagePrevSlider::-webkit-scrollbar {
  cursor:grab;
 
  width: 5px;
}

.ImagePrevSlider::-webkit-scrollbar-track {
  background: transparent;
}

.ImagePrevSlider::-webkit-scrollbar-thumb {
  background:  linear-gradient(to bottom,#60A5FA,#2a8aff);
  border-radius: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@keyframes slide-up {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
  }
}

.animate-slide-up {
  animation: slide-up 0.5s ease-out forwards;
}



